.lesson-container {
  

  .lesson-error-container {
    background-color: #fce4e4;
    border: 1px solid #fcc2c3;
    padding: 20px 30px;
    text-align: center;
  }

  .lesson-error-text {
    color: #cc0033;
    font-family: Helvetica, Arial, sans-serif;
    font-size: 13px;
    font-weight: bold;
    line-height: 20px;
    text-shadow: 1px 1px rgba(250,250,250,.3);
  }

  .lesson-container-title {
    text-align: center;

    h1 {
      margin-bottom: 2rem;
    }

    em {
      display: block;
      max-width: 35rem;
      margin: 2rem auto 1rem auto;
      font-size: 12pt;
      text-decoration: none;
      color: initial;
    }

  }

}

.lesson-content-container {
  overflow: 'scroll';
  overflowY: 'scroll';
}

