.topnavbar-container {

  .scrolled {
    position: fixed;
    top: 0;
    left: 0;
    background-color: lightblue;
    box-shadow: 10px 10px 8px #888888;
  }


  li, li a {
    color:#000; 
    text-decoration:none;
  }

  .nav li { 
    width:100%; 
    text-indent:10px; 
    line-height:30px; 
    margin-right:10px; 
    border-top:1px #000 solid; 
    border-bottom:1px #000 solid; 
    border-left:none; border-right:none; 
    background:#fff;
  }

  li a {
    display:block; 
    width:inherit; 
    height:inherit;
  }

  ul.nav { display:none; }
  
  .topnavbar li:hover > a, .topnavbar li:hover { 
    color:#fff; 
    background:#888888; 
  }
  
  li:hover > .nav { 
    display:block;
    position:absolute; 
    list-style: none;
    width:200px; 
    left:20%; 
    z-index:1000; 
    border:1px #000 solid; 
  } 

  li:hover { 
    position:relative; 
    z-index:2000; 
  } 

  .topnavbar {
    width: 100%;
    min-height: 6vh;
    border-bottom: 1px solid #eaecef;
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    justify-content: center;
    box-sizing: border-box;
    align-items: center;
    background-color: #eee;
    transition: all .7s ease-in;

    ul {
      margin: 0;
      padding-left: 0;
    }

    li {
      float: left;
      
      button {
        float: left;
        font-size: 1.2em;
        width: 100%;
        cursor: pointer;
        outline: none;
        border: none;
        align-items: center;
      }  

      button:hover {background-color: #3eaf7c}

      button:active {
        transform: translateY(4px);
      }

      a {
        text-decoration: none;
        font-weight: bold;
        color: #069;
        border-right: 1px solid #ccc;
        display: block;
        padding: 1rem;
        position: relative;
        text-decoration: none;
        font-weight: bold;
        color: #069;
        border-right: 1px solid #ccc;
        transition-duration: 0.5s;
      }
    
      a:link, a:visited, a:focus {
        color: inherit;
        text-decoration: none;
        border-bottom: 2px solid transparent;
      }

      a:hover {
        border-bottom: 2px solid #46bd87;
        cursor: pointer;
      }

    }

    .logo {
      height: 2.2rem;
    }

  }
}

